/*
//------------------------------------------------------------------------------
// Layouts 1
//------------------------------------------------------------------------------
*/

.layout-1{
  .pane-content{
    h1{
      font-family: $crimson-textbold;
      @include font-size(l);
      color: color('grey-dark');
      text-align: left;
      padding: 10px;
      font-weight: 600;
      &:after{
        content: "";
        display: block;
        height: 2px;
        width: 80px;
        border-bottom: 1px solid #484851;
      }
    }
  }
}
.slider-homes-layout-one{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  .conten{
    padding: 0 25px;
    max-width: 500px;
    margin-left: auto;
    margin-right: auto;
    .title-slider-homes,
    .body-slider-homes,
    .link-slider-homes {
      text-align: center;
    }
  }
}

/* ------| Slider centarado |----- */
.view-id-banner_nodes {

  .slider-homes-layout-one-title {
    position: absolute;
    top: 20%;
    width: 100%;
    height: auto;
    div:nth-child(1) {
      font-family: $crimson-textitalic;
      @include font-size(l);
      color: color('white');
      @include respond-to(xl) {
        @include font-size(xxl);
      }
    }

    div:nth-child(2) {
      font-family: $latoblack;
      @include font-size(xl);
      color: color('white');
      @include respond-to(xl) {
        @include font-size(xxxl);
      }
    }
    div:nth-child(3) {
      font-family: $latoregular;
      @include font-size(l);
      color: color('white');
      @include respond-to(xl) {
        @include font-size(xxxl);
      }
    }
    div:nth-child(4) {
      font-family: $crimson-textregular;
      @include font-size(m);
      color: color('white');
    }
  }
}