/*
//------------------------------------------------------------------------------
// Layouts 9
//------------------------------------------------------------------------------
*/

.slider-homes-layout-nine{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  .conten{
    padding: 0 25px;
    max-width: 500px;
    margin-left: auto;
    margin-right: auto;
    .title-slider-homes,
    .body-slider-homes,
    .link-slider-homes {
      text-align: center !important;
    }
  }
}

/* ------| Panel Responsin |----- */
#layout-nine {
  .panel-col-first {
      width: 100%;
    @include respond-to(m) {
      width: 50%;
    }
  }
  .panel-col-last {
    width: 100%;
    @include respond-to(m) {
      width: 49.9%;
    }
  }
}

/* ------| layaut first, last |----- */
#layout-nine .row-inside-first {
  //titulo
  .pane-node-title {
    h1 {
    font-family: $latoblack;
    @include font-size(l);
    color: color('grey');
    text-align: left;
    text-transform: uppercase;
    font-weight: bold;
    padding-left: 15px;
    @include respond-to(xs) {
        @include font-size(xl);
      }
      @include respond-to(xl) {
        @include font-size(xxxl);
      }
    }
  }
  //body
  .field-name-body {
    padding: 0 15px;
    p {
      font-family: $crimson-textregular;
      @include font-size(o);
      color: color('black');
    }
  }
  //Mapa
  .field-name-field-lugar {
    margin-top: 40px;
    // @include respond-to(m) {
    //   margin-top: 0px;
    // }
  }
}



