/*
//------------------------------------------------------------------------------
// Home Especiales
//------------------------------------------------------------------------------
*/

/* ------| quicktabs Tabs |----- */
#quicktabs-quicktabs_especiales {
	ul.quicktabs-tabs {
		width: 100%;
		padding: 0;
		margin: 0;
		padding-bottom: 40px;
		list-style: none;
		@include respond-to(xl) {
			margin: 0 10px;
		}
		li {
			margin: 10px 0;
			display: block;
			@include respond-to(xs) {
				display: inline-block;
				margin: 0px;
			}
			a {
				font-family: $latobold;
				@include font-size(s);
				text-align: center;
				text-decoration: none;
				background-color: #f0eeee;
				padding: 15px 50px;
				display: block;
				cursor: pointer;
			}
			&.active {
				a {
					background-color: color('blue-light');
					color: color('white');
				}
			}
			&:hover {
				a {
					background-color: color('blue-light');
					color: color('white');
				}
			}
		}
	}
}

/* ------| Eventos |----- */
.view-display-id-especiales_recientes,
.view-display-id-especiales_visitas {
	display: block;
	overflow: hidden;
	.view-content {
		.views-row {
			.views-column {
				margin-bottom: 15px;
				//Img
				.views-field-view {
					@include respond-to(xs) {
						max-height: 150px;
					}
					.views-field {
						img{
							width: 100%;
						}
						.youtube-container--responsive {
							padding-bottom: 62%;
						}
					}
				}
				.box-content {
					position: relative;
					padding: 20px 15px;
					border-top: 3px solid #ff8827;
					background: #f4f1f1;
					@include respond-to(xs) {
						min-height: 360px;
					}
					//Fecha
					.views-field-created {
						font-family: $latoregular;
						@include type-layout(xs, 1.23);
						color: color('orange');
						padding: 5px 0px;
					}
					//Titulo
					.views-field-title {
						font-family: $crimson-semibold;
						@include type-layout(sl, .90);
						// color: #484851;
						margin: 15px 0 20px;
						@include respond-to(m) {
							@include font-size(ll);
						}
					}
					//Body
					.views-field-body {
						font-family: $crimson-textregular;
						@include font-size(m);
						margin-bottom: 16px;
						@include respond-to(xs) {
							height: 7pc;
						}
					}
					//Button
					.views-field-view-node {
						a {
							display: block;
							max-width: 180px;
							font-family: $latoregular;
							@include font-size(m);
							color: #484851;
							text-decoration: none;
							text-align: center;
							border: 1px solid color('orange');
							padding: 10px 20px;
							max-width: 180px;
							margin: 0 auto;
							@include respond-to(xs) {
								position: absolute;
								bottom: 25px;
								right: 0;
								left: 0;
							}
							&:hover {
								background-color: color('orange');
								color: #ffffff;
							}
							@include respond-to(m) {
								right: 25px;
								left: inherit;
								max-width: 150px;
							}
						}
					}
				}
			}
			@media screen and (min-width: 555px) and (max-width: 767px) {
				.views-column {
					width: 50%;
					float: left;
				}
			}
		}
	}
}

/* ------| Paguinadores |----- */
.view-display-id-especiales_recientes,
.view-display-id-especiales_visitas {
	.text-center {
		width: 100%;
		position: relative;
		overflow: hidden;
		ul.pagination {
			background: color('white');
			border-radius: 0;
			padding: 10px;
			&::before {
				content: '';
				display: block;
				position: absolute;
				left: 0;
				right: 0;
				top: 50%;
				width: 100%;
				height: 1px;
				border-top: 1px solid color('blue-light');
				z-index: -1;
			}
			li {
				color: #484851;
				border: 1px solid #fff;
				border-radius: 0;
				background-color: #fff;
				display: block;
				float: left;
				margin: 0 5px;
				a {
					color: #484851;
					border: 1px solid #fff;
					font-family: Lato,sans-serif;
					border-radius: 0;
					background-color: #fff;
					.ajax-progress {
						display: none;
					}
				}
				&.active {
					border-color: color('blue-light');
					span {
						background-color: transparent;
						border-radius: initial;
						color: color('black');
						border: initial;
					}
				}
				&.next,
				&.prev,
				&.pager-last,
				&.pager-first {
					display: none;
				}
			}
		}
	}
}